<template>
  <el-form
    :model="userinfo"
    status-icon
    :rules="rules"
    ref="userinfo"
    label-width="100px"
    class="login-form"
  >
    <el-form-item label="用户名" prop="username">
      <el-input v-model="userinfo.username" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input type="password" v-model="userinfo.password" autocomplete="off"></el-input>
    </el-form-item>
    <!-- <el-form-item label="年龄" prop="age">
      <el-input v-model.number="userinfo.age"></el-input>
    </el-form-item>-->
    <el-form-item>
      <el-button type="primary" @click="submitForm('userinfo')">提交</el-button>
      <el-button @click="resetForm('userinfo')">重置</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("年龄不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (value < 18) {
            callback(new Error("必须年满18岁"));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.userinfo.checkPass !== "") {
          this.$refs.userinfo.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.userinfo.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      userinfo: {
        username: "",
        password: ""
      },
      rules: {
        username: { required: true, message: "请填用户名", trigger: "blur" },
        password: { required: true, message: "请填写密码", trigger: "blur" }
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        
        if (valid) {
          this.$http
          .post("http://localhost:3000/api/public/login", this.userinfo)
          .then(response => {
            console.log(response.data.token);
            sessionStorage.setItem('token', response.data.token);
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
     this.$http
          .get("http://localhost:3000/api/users")
          .then(res => {
              console.log(res)
          })
    }
  }
};
</script>
<style lang="scss" scoped>
.login-form {
  width: 80%;
  margin: 20px 0;
}
</style>
